import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import Img from "../../assets/imagemAssinatura.png";
import app from "../../auth/config";
import Layout from "../../layout";
import "./style.css";
import * as S from "./styles";
import firebase from "firebase"

function Assinatura() {
  const history = useHistory();
  // const { updateSignature, userIsAuthenticated, signature } = useAuth();
  // const { payFree } = usePayments();
  const userIsAuthenticated = app.auth().currentUser;
  const isSignature =
    app.auth().currentUser && localStorage.getItem("signature");
  // const { addToPayment } = usePayments();
  const [plans, setPlans] = useState([]);
  const [free, setFree] = useState({});
  function formatcurrency(number) {
    let str = number.split("$")[1];
    str = str.split(".");
    return str;
  }
  useEffect(() => {
    async function getPlan() {
      try {
        const { data } = await axios.get(
          "https://json-serverepw.herokuapp.com/plains"
        );
        setPlans(data);
        const planFree = [].find(
          (plano) => plano.id === "28d47f87-3dc0-4660-8f2c-261f0484586e"
        );
        setFree(planFree);
      } catch (error) {
        console.log(error);
      }
    }

    // if(userIsAuthenticated) {
    //   const userId = firebase.auth().currentUser.uid;
      
    //   let email = firebase.database().ref('users/' + firebase.auth().currentUser.uid + "/signature");
    //   email.on('value', (snapshot) => {
    //     if (snapshot.val() !== null) {
    //       console.log(snapshot.val());
    //     }
    //   })

    // }
    
    getPlan();
  }, []);

  function payment(plano, isTesteGratis){
    // firebase -> dados usuario -> data/hora  ## salvar
    const userId = firebase.auth().currentUser.uid;
    const data = new Date().toLocaleString("en-US", {timeZone: 'America/Sao_Paulo'})

    if(isTesteGratis) {
      // firebase.database().ref('users/' + userId).update({
      //   signature: true
      // })
      firebase.database().ref('assinaturas/' + userId).update({
        userId, data, id: "28d47f87-3dc0-4660-8f2c-261f0484586e", name: "Mensal", value: "$19.97"
      })
      window.location.href = "http://pag.ae/7XLfTiyXa";
    } else {
      // firebase.database().ref('users/' + userId).update({
      //   signature: true
      // })
      firebase.database().ref('assinaturas/' + userId).update({
        userId, data, id: plano.id, name: plano.name, value: plano.value 
      })
      window.location.href = plano.paymentLink;
    }

  }

  console.log(isSignature)

  return (
    <Layout>
     
        <S.Wrapper>
          <S.Container>
            <>
              <S.Header>
                <S.Div>
                  <span>Teste grtis por 7 dias</span>
                  <br />
                  <br />
                  <S.HeaderSpan>
                    Aproveite desconto em lanamentos e o nosso catlogo com
                    contedo exclusivo e ilimitado em peas teatrais, stand-ups,
                    shows e palestras.
                  </S.HeaderSpan>
                  <br />
                  <br />
                  <S.ButtonPagar
                    onClick={() => {
                      userIsAuthenticated
                          ? (payment({}, true))
                          : history.push("/LoginAssinatura");
                    }}
                  >
                    INICIAR PERODO DE TESTE
                  </S.ButtonPagar>
                </S.Div>
                <S.Div>
                  <img className="img" alt="" src={Img}></img>
                </S.Div>
              </S.Header>

              <S.HeaderSpan>Escolha o melhor plano para voc:</S.HeaderSpan>
              <br />
              <S.CartActions>
                <S.ButtonVoltar onClick={() => history.push(`/`)}>
                  <i className="bi bi-chevron-left"></i>Voltar{" "}
                </S.ButtonVoltar>
                {plans?.map((plano) => {
                  return (
                    <S.ButtonPagar
                      key={plano.id}
                      onClick={() => {
                        userIsAuthenticated
                          ? (payment(plano, false))
                          : history.push("/LoginAssinatura");
                      }}
                    >
                      <div className="plano">{plano.name}</div> <br />
                      <div className="valorPlano">
                        <div className="valorContainer">
                          <div className="valorPequeno">R$</div>
                          {formatcurrency(plano.value)[0]}
                          <div className="valorPequeno">
                            ,{formatcurrency(plano.value)[1]}
                          </div>
                        </div>
                      </div>{" "}
                      <br />
                      ASSINAR
                    </S.ButtonPagar>
                  );
                })}
              </S.CartActions>
            </>
            <S.CartInfo>
              <span>
                Torne-se um assinante e garanta 50% de desconto neste contedo
              </span>
              <S.CartInfoText>
                E ainda tenha acesso a um amplo contedo em peas teatrais,
                shows musicais e palestras para assistir a qualquer hora(exceto
                lanamentos).
              </S.CartInfoText>
            </S.CartInfo>
          </S.Container>
        </S.Wrapper>
      
    </Layout>
  );
}

export default Assinatura;
