import React, { useState } from "react";
import { useHistory } from "react-router";
import { SwiperSlide } from "swiper/react";
import app from "../../auth/config";
import Card from "../../components/Card";
import Footer from "../../components/Footer";
import Header from "../../components/Header";
import Section from "../../components/Section";
import lista from "../Home/lista";
import * as S from "./styles";
import * as SCard from "../Canais/styles";
import HorizontalList from "../../components/HorizontalList";
import firebase from "firebase"


function AssinaturaConteudo() {
  const history = useHistory();
  // const [isSignature, setIsSignature] = useState(false)
  let email = firebase.database().ref('users/' + firebase.auth().currentUser.uid + "/signature");
      email.on('value', (snapshot) => {
        if (!snapshot.val()) {
          history.push("/Assinatura");
        }
      })

  console.log(lista)

  return (
    <>

      <Header />

      <S.Container>
        <h1>
          Sua assinatura estar disponvel em at 24 horas. Por favor, aguarde.
        </h1>

        <h2>Meus Espetculos</h2>
        <Section title="Meus Espetculos" filter>
                    <HorizontalList id="emAlta">
                      {lista?.reverse().map((card, index) => {
                              return (
                                <SwiperSlide key={String(index)}>
                                  {card.isActiveOnDemand === false &&                                 
                                  <S.CardTest>
                                      <S.Img
                                        src={card.thumbnail}
                                        style={{
                                          position: "absolute",
                                        }}
                                        value={card.key}
                                        alt="imagem referente a pea"
                                      />
                          
                                      <S.InfoCard>
                                        <S.InfoCardConteudo>
                                          <S.Classificacao>
                                            {card.classificacao}
                                          </S.Classificacao>
                                          <S.Coracao
                                            // onClick={() => {
                                            //   fav(card);
                                            // }}
                                          >
                                            <i className="bi bi-suit-heart-fill"></i>
                                          </S.Coracao>
                          
                                          <S.Compartilhar>
                                            {/* <i
                                              className="bi bi-share-fill"
                                              onMouseEnter={() =>
                                                setcompartilhar(true)
                                              }
                                            ></i> */}
                                            {/* {compartilhar && (
                                              <Share
                                                sair={setcompartilhar}
                                                link={`https://www.palcoweb.com.br/#/cardezao/${card.key}`}
                                              ></Share>
                                            )} */}
                                          </S.Compartilhar>
                                        </S.InfoCardConteudo>
                          
                                        <S.Titulo>{card.title}</S.Titulo>
                                        {/* <S.ConteudoCard>
                                          Com {card.Artista}
                                        </S.ConteudoCard> */}
                                        {/* <S.ConteudoCard>
                                          <i className="bi bi-star-fill"></i>{" "}
                                          <i className="bi bi-star-fill"></i>{" "}
                                          <i className="bi bi-star-fill"></i>
                                          <i className="bi bi-star-fill"></i>
                                        </S.ConteudoCard> */}
                                        <S.ConteudoCard
                                          style={{
                                            textTransform: "uppercase",
                                          }}
                                        >
                                          {card.categoria}
                                        </S.ConteudoCard>
                                        <S.Sinopsia>{card.synopsis}</S.Sinopsia>
                          
                                        <S.ButtonAcessar
                                          className="btn btn-warning"
                                          onClick={() => {
                                            history.push(`cardezao/${card.key}`);
                                            //window.scrollTo(0, 0);
                                            window.location.reload();
                                          }}
                                        >
                                          Saiba Mais{" "}
                                        </S.ButtonAcessar>
                                      </S.InfoCard>
                                    </S.CardTest>
                                  } 
                                </SwiperSlide>
                              );
                            })

                          }

                    </HorizontalList>
                  </Section>
      </S.Container>
      <Footer /> 
    </>
  );

  // user={
  //   userIsAuthenticated() && signature?.isActive
  //     ? "isSignature"
  //     : "onDemand"
  // }
}

export default AssinaturaConteudo;
